import VueRouter from 'vue-router';

import loginPage from "@/pages/loginPage";
import ShowPages from "@/pages/ShowPages";

const Route = new VueRouter({
    mode: "history",
    // base: process.env.BASE_URL
    // ,
    routes: [

        {
            path: "/",
            redirect: "/login"
        },
        {
            name: "logingPage",
            path: "/login",
            component: loginPage
        }, {
            name: "showAll",
            path: "/show",
            component: ShowPages
        },
    ]
})
Route.beforeEach((to, from, next)=>{
    if (to.path==="/show"&&from.path==="/login"){
    next()
    }else {
        if (to.path==="/login"){
            next()
        }
        next("/login")
    }
    // console.log(to)
    // console.log(from)
    // next()

})

export default Route