<template>

<div class="headInput">
<inputAera_1 activeName="用户名" :updateMethod="userIDUpdate" type="text"/>
  <br>
  <inputAera_1 activeName="密码" :updateMethod="passwordUpdate" type="password"/>
  <br>
  <el-button type="primary" @click="login(user)" round>登录</el-button>
  <br>
</div>

</template>

<script>
import inputAera_1 from "../components/inputAera";

export default {
  name: "loginPage",
  components: {inputAera_1},
data(){
    return{
      user:{
        name:"",
        password:""
      }
    }
},
  methods:{
    // eslint-disable-next-line no-unused-vars
    login(user){
      let URL = window.location.origin + '/user/login'
      // URL="http://localhost:8082/user/login"
      fetch(URL,{
        method: "POST",
        headers:{
          "Content-Type":"application/json"
        },
        body:JSON.stringify(user)
      }).
      then(response=>response.json())
      .then((res)=>{
        if (res.code!==200){
          this.$message({
            message:res.message,
            type:"error"
          })
        }else {
          this.$message({
            message: res.message,
            type: 'success'
          });
          this.$router.push({
            path:"/show"
          })



        }
      })

    },
    userIDUpdate(data){
      this.user.name=data
    },
    passwordUpdate(data){
      this.user.password=data
    }
  },
  mounted() {
    this.$bus.$on("login",this.login)
  }
}
</script>

<style scoped>
.headInput{
  min-height: 100%;
  width: 60%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin: 0 auto;
  border: 1px solid #909399;
  border-radius: 50px;

}
</style>