<template>
<div class="deflautDIV">
  <el-table
      :data="datas"
      style="width: 100%">
    <el-table-column
        prop="name"
        label="姓名"
        width="180">
    </el-table-column>
    <el-table-column
        prop="sex"
        label="性别"
        width="250">
    </el-table-column>
    <el-table-column
        prop="age"
        label="年龄"
        width="250">
    </el-table-column>
  </el-table>

  <el-pagination
      @current-change="nextPage"
      :hide-on-single-page="count===1"
      background
      layout="prev, pager, next"
      :total="count">
  </el-pagination>
</div>
</template>

<script>
export default {
  name: "ShowPages",
  data(){
    return{
      datas:[],
      start:0,
      size:10,
      count:0
    }
  },
methods:{
    getDatas(){
      let URL=window.location.origin+`/user/select?start=${this.start-1}&size=${this.size}`
      // URL=`http://localhost:8082/user/select?start=${this.start-1}&size=${this.size}`
      fetch(URL).then(response=>response.json())
      .then((response)=>{
        this.datas=response.data
      })
    },
  nextPage(data){
    this.start=data
    this.getDatas()
  }
},
  mounted() {
  this.getDatas()
    let URL=window.location.origin+`/user/getCount`
    // URL=`http://localhost:8082/user/getCount`
    fetch(URL).then(response=>response.json())
        .then((response)=>{
          this.count=response.data
        })
  }
}
</script>

<style scoped>
.deflautDIV{
  min-height: 100%;
  width: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin: 0 auto;
  /*border: 1px solid #909399;*/
  border-radius: 50px;
}
</style>