<template>
<div class="inputs">

<label>
  {{activeName}}
</label>
  <el-input :type="type" v-model="input" :placeholder="activeName"  @change="dateUpdate"></el-input>

</div>
</template>

<script>
export default {

  name: "inputAera_1",
  props:["activeName","updateMethod","type"],
  data(){
    return{
      input:''
    }
  },
  methods:{
    dateUpdate(){
      this.updateMethod(this.input)
    }
  }

}
</script>

<style scoped>
.inputs{
  width: 80%;
}

</style>